<template>
    <nav class="navbar is-primary is-fixed-top">
        <div class="navbar-brand">
            <router-link :to="{ name: 'Home' }" class="navbar-item" exact>
                <img alt="logo" src="../assets/logo.png">
                {{ appName }}
            </router-link>
            <div class="navbar-burger burger" data-target="navbarExampleTransparentExample">
                <span></span>
                <span></span>
                <span></span>
            </div>
        </div>

        <div id="navbarExampleTransparentExample" class="navbar-menu">
            <div class="navbar-start">
                <div class="navbar-item" v-if="loading">
                    <span v-if="loading" class="icon is-small is-left">
                        <i class="fas fa-spinner" aria-hidden="true"></i>
                    </span>
                    <span>&nbsp;Loading...</span>
                </div>
                <div class="navbar-item has-dropdown is-hoverable" v-else>
                    <router-link
                        :to="{ name: 'CompetitionsTable' }"
                        class="navbar-link">
                        {{ leagues.length }} Leagues
                    </router-link>
                    <div class="navbar-dropdown is-boxed">
                        <router-link
                            v-for="league in leagues"
                            :key="league.id"
                            :to="{ name: 'LeagueTable', params: { 'competition': league.id } }"
                            class="navbar-item">
                            {{ league.caption }}
                        </router-link>
                    </div>
                </div>
            </div>

            <div class="navbar-end">
            <div class="navbar-item">
                <div class="field is-grouped">
                <p class="control">
                    <a class="button is-info" href="https://www.football-data.org/" target="_blank" rel="noopener">
                        <span class="icon"><i class="fas fa-link"></i></span>
                        <span>football-data</span>
                    </a>
                </p>
                </div>
            </div>
            </div>
        </div>
    </nav>
</template>
<script>
import { mapGetters } from 'vuex';
import BulmaBurger from '@/helpers/BulmaBurger';
import { APP_NAME } from '@/config';
import { LEAGUES } from '@/store/modules/competitions/loading-types';

export default {
  name: 'Navbar',
  mounted() {
    BulmaBurger();
    this.$store.dispatch('fetchLeagues', {});
  },
  computed: {
    appName() {
      return APP_NAME;
    },
    loading() {
      return this.$store.getters.isLoading(LEAGUES);
    },
    ...mapGetters({
      leagues: 'getLeagues',
    }),
  },
};
</script>



