import * as type from './mutation-types';

export default {
  [type.LOADING]: ({ loading }, payload) => {
    const idx = loading.indexOf(payload.type);

    if (idx !== -1 && payload.status === false) {
      loading.splice(idx, 1);
    } else {
      loading.push(payload.type);
    }
  },
  [type.LEAGUES]: (state, payload) => state.leagues = payload,
  [type.LEAGUE_TABLE]: ({ league }, payload) => {
    if (payload.standing) {
      league.table = payload;
    }
  },
  [type.LEAGUE_TEAMS]: ({ league }, payload = {}) => league.teams = payload.teams ? payload.teams : [],
  [type.LEAGUE_FIXTURES]: ({ league }, { fixtures = [] }) => {
    if (fixtures.length) {
      const teamsMap = new Map();

      league.teams.forEach((team) => teamsMap.set(team.name, team));

      fixtures.forEach((fixture) => {
        const homeTeam = teamsMap.get(fixture.homeTeamName);
        const awayTeam = teamsMap.get(fixture.awayTeamName);

        if (fixture.homeTeamName === homeTeam.name) {
          fixture.homeTeamId = homeTeam.id;
          fixture.homeTeamCrest = homeTeam.crestUrl;
        }
        if (fixture.awayTeamName === awayTeam.name) {
          fixture.awayTeamId = awayTeam.id;
          fixture.awayTeamCrest = awayTeam.crestUrl;
        }
      });
    }

    const sorted = fixtures.slice().sort((fixtureA, fixtureB) => (new Date(fixtureB.date) - new Date(fixtureA.date)));

    league.fixtures = sorted;
  },
  [type.TEAM]: (state, team) => state.team = team,
  [type.TEAM_PLAYERS]: (state, { players }) => state.players = players,
};



