import Vue from 'vue';
import Router from 'vue-router';

const Home = () => import ('@/components/Home');

const Competitions = () => import ('@/components/Competitions');
const CompetitionsTable = () => import ('@/components/CompetitionsTable');

const League = () => import('@/components/League');
const Fixtures = () => import('@/components/Fixtures');
const LeagueTable = () => import('@/components/LeagueTable');
const LeagueFixtures = () => import('@/components/LeagueFixtures');

const Team = () => import ('@/components/Team');
const Player = () => import ('@/components/Player');

Vue.use(Router);

export default new Router({
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 }
  },
  linkActiveClass: 'is-active',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/competitions',
      component: Competitions,
      children: [
        {
          path: '',
          name: 'CompetitionsTable',
          component: CompetitionsTable,
        },
        {
          path: ':competition',
          name: 'League',
          component: League,
          props: true,
          children: [
            {
              path: 'table',
              name: 'LeagueTable',
              component: LeagueTable,
              props: true,
            },
            {
              path: 'fixtures',
              component: LeagueFixtures,
              name: 'LeagueFixtures',
              props: true,
              children: [
                {
                  path: 'all',
                  name: 'AllFixtures',
                  component: Fixtures,
                },
                {
                  path: 'finished',
                  name: 'FinishedFixtures',
                  component: Fixtures,
                  props: {
                    status: 'FINISHED',
                  },
                },
                {
                  path: 'timed',
                  name: 'TimedFixtures',
                  component: Fixtures,
                  props: {
                    status: 'TIMED',
                  },
                },
                {
                  path: 'scheduled',
                  name: 'ScheduledFixtures',
                  component: Fixtures,
                  props: {
                    status: 'SCHEDULED',
                  },
                },
                {
                  path: 'cancelled',
                  name: 'CancelledFixtures',
                  component: Fixtures,
                  props: {
                    status: 'CANCELLED',
                  },
                },
              ],
            },
          ],
        },
        {
          path: 'team/:id',
          name: 'Team',
          component: Team,
          props: true,
        },
      ],
    },
  ],
});



