import Vue from 'vue';
import Raven from 'raven-js';
import RavenVue from 'raven-js/plugins/vue';
import {
  SENTRY_KEY,
  SENTRY_PROJECT,
} from '@/config/errorHandling';

if (
  SENTRY_KEY &&
  SENTRY_PROJECT &&
  SENTRY_KEY.length > 0 &&
  SENTRY_PROJECT.length > 0
) {
  Raven
    .config(`https://${SENTRY_KEY}@sentry.io/${SENTRY_PROJECT}`)
    .addPlugin(RavenVue, Vue)
    .install();
}



