import axios from 'axios';
import {
  API_KEY,
  API_VERSION,
  API_ENDPOINT,
  acceptedParams,
} from '@/config/api';
import helpers from './helpers';

// Default API configurations
axios.defaults.baseURL = `${API_ENDPOINT}/${API_VERSION}/`;
axios.defaults.headers['X-Response-Control'] = 'minified';

// Check if API key is setted before send request
axios.interceptors.request.use(
  (response) => {
    response.headers['X-Auth-Token'] = API_KEY;
    if (!response.headers['X-Auth-Token'].length) {
      return Promise.reject(':: API key must be defined.');
    }

    return response;
  },
  error => Promise.reject(error),
);

/**
 * Return a specific fixture
 *
 * @param Number fixture
 * @param {*} params
 * @returns Promise
 */
const getFixture = (fixture, params = {}) => {
  const uri = 'fixture/fixture';
  if (!helpers.paramsAreValid(uri, params)) {
    return new Promise((accept, reject) => reject(`:: Some params are not valid. Valid ones are: ${acceptedParams[uri]}`));
  }

  const defaults = {};
  return axios.get(`fixtures/${fixture}`, { params: Object.assign(defaults, params) });
};

/**
 * Returns all fixtures from all competitions
 *
 * @param {*} params
 * @returns Promise
 */
const getFixtures = (params = {}) => {
  const uri = 'fixtures';
  if (!helpers.paramsAreValid(uri, params)) {
    return new Promise((accept, reject) => reject(`:: Some params are not valid. Valid ones are: ${acceptedParams[uri]}`));
  }

  const defaults = {};
  return axios.get('fixtures', { params: Object.assign(defaults, params) });
};

/**
 * Returns all competitions
 *
 * @param {*} params
 * @returns Promise
 */
const getLeagues = (params = {}) => {
  const uri = 'competitions';
  if (!helpers.paramsAreValid(uri, params)) {
    return new Promise((accept, reject) => reject(`:: Some params are not valid. Valid ones are: ${acceptedParams[uri]}`));
  }

  const defaults = {
    season: new Date().getFullYear() - 1,
  };
  return axios.get('competitions', { params: Object.assign(defaults, params) });
};

/**
 * Return a specific competition's table
 *
 * @param Number competition
 * @param {*} params
 * @returns Promise
 */
const getLeagueTable = (competition, params = {}) => {
  const uri = 'competitions/competition/leagueTable';
  if (!helpers.paramsAreValid(uri, params)) {
    return new Promise((accept, reject) => reject(`:: Some params are not valid. Valid ones are: ${acceptedParams[uri]}`));
  }

  const defaults = {};
  return axios.get(`competitions/${competition}/leagueTable`, {
    headers: {
      'X-Response-Control': 'full',
    },
    params: Object.assign(defaults, params),
  });
};

/**
 * Return a specific competition's table with teams
 *
 * @param Number competition
 * @param {*} params
 * @returns Promise
 */
const getLeagueTableWithTeams = (competition, params = {}) => {
  return axios.all([getLeagueTable(competition, params), getLeagueTeams(competition, params)])
    .then(axios.spread((table, teams) => {
      return {
        table: table.data,
        teams: teams.data.teams,
      };
    }));
};

